
/*imports */
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400');

/* main */

body {
  font-family: 'Roboto', cursive;
  font-weight: 100;
}

.App {
  text-align: center;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.bg {
  background-image: url(../images/decorative-circle-half.png);
  background-repeat: no-repeat;
  background-size:80%;
  background-position: 250% 0%;
  background-attachment:fixed;

  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main_wrapper {
  flex-direction: row;
}
.txt_1{
  font-size:30px;/*better to use relative*/
  color: #3f4a75;
  width:300px;
  text-align: left;
}
.txt_2{
  font-size:30px;
  color: #db487e;
}
.list {
  font-size:20px;
  color: #3f4a75;
}
.listItem {
  padding-left: 0 !important;
  padding-bottom: 0 !important;
}
.pic_1_1 {
  width: 600px;
  height: 600px;
  padding:50px;
} 
.pic_1 {
  /*width:50%;*/
  width: 100%;
  height: 100%;
  background-image: url(../images/64152862_mask.png) ;
  background-size: cover;
  border-radius: 50%;
  /* flex */
  display: flex;
  flex-direction: column;
}
.inner_row{
  /* border: 1px solid red; */
  width:100%;
  
}
.row_1 {
  height: 250px;
  display:flex;
  justify-content: center;
  margin-top:-4px;
}
.row_2 {
  height: 588px;
  padding-top:34px;
  display: flex;
  justify-content: space-between; 
  margin-left: 44px;
  
}
.row_3 {
  height: 100%;
  
  display: flex;
  align-items:flex-end;
  justify-content: space-around; 
}
.btn {
  /* border: 1px solid red; */
  width: 100px;
  height:100px;
  /* padding:20px; */
}
.btn_lh {
  display: flex;
  justify-content: flex-start;
  margin-left: -20px;
}
.btn_rh {
  display: flex;
  justify-content: flex-start;
  margin-right: 25px;
}
.btn_btm_lh {
  justify-content: flex-end;
  padding-right: 10px;
  /* margin-bottom: -20px; */
}
.btn_btm_rh {
  justify-content: flex-end;
  padding-left: 100px;
  /* margin-bottom: -20px; */
}
.fab {
  border:2px solid #3f4a75 !important;
}


.App-header {
  /*background-color: pink;*/
  
  /*background-size: 'contain';*/
  
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
